import React, {
  useContext,
  useState,
  useRef,
  FormEvent,
  useReducer,
  useEffect,
} from "react";
import {
  Box,
  Typography,
  TextField,
  Button,
  CircularProgress,
} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import DeleteIcon from "@mui/icons-material/Delete";
import useUrlParams from "../../../hooks/useUrlParams";
import { GridRowSelectionModel } from "@mui/x-data-grid";
import DataGridTable from "../../../components/DataGridTable";
import { pink } from "@mui/material/colors";
import { AuthContext } from "../../../components/AuthContext";
import { useMutation, useQuery, useQueryClient } from "react-query";
import Swal from "sweetalert2";
import { wait } from "../../../lib/wait";
import { throttle } from "lodash";
import { green } from "@mui/material/colors";
import CloseIcon from "@mui/icons-material/Close";
import SaveIcon from "@mui/icons-material/Save";
import { Dropdown } from "../../../components/DropdownButton";
import Autocomplete from "@mui/material/Autocomplete";

const initialState = {
  Mortgagee: "",
  Policy: "",
};
export const reducer = (state: any, action: any) => {
  switch (action.type) {
    case "UPDATE_FIELD":
      return {
        ...state,
        [action.field]: action.value,
      };
    default:
      return state;
  }
};

export const poliyAccountColumn = [
  { field: "Policy", headerName: "Policy", flex: 1 },
  { field: "Mortgagee", headerName: "Mortgagee", flex: 1 },
  { field: "createdAt", headerName: "Created At", flex: 1 },
];
const queryKey = "mortgagee-account";
export default function Mortgagee() {
  const { searchParams, setSearchParams } = useUrlParams();
  const [state, dispatch] = useReducer(reducer, initialState);
  const { myAxios, user } = useContext(AuthContext);
  const [exportMode, setExportMode] = useState(false);
  const [loadingSearch, setLoadingSearch] = useState(false);
  const [save, setSave] = useState(searchParams.get("selected") !== "");
  const [addNew, setAddNew] = useState(searchParams.get("selected") !== "");
  const [deleting, setDeleting] = useState(searchParams.get("selected") !== "");
  const [rows, setRows] = useState<GridRowSelectionModel>([]);
  const submitButton = useRef<HTMLButtonElement>(null);
  const [hasSelected, setHasSelected] = useState(
    searchParams.get("selected") !== ""
  );
  const queryClient = useQueryClient();

  const { data, isLoading } = useQuery({
    queryKey,
    queryFn: async () =>
      await myAxios.get(
        `/reference/get-mortgagee?mortgageeSearch=${
          searchParams.get("mortgageeSearch") as string
        }`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      console.log(res)
      setRows((res as any)?.data.mortgagee.mortgagee);
    },
  });
  const {
    mutate,
    isLoading: loadingAddNew,
    variables,
  } = useMutation({
    mutationKey: queryKey,
    mutationFn: async (variables: any) => {
      if (variables.mode && variables.mode === "delete") {
        return await myAxios.post("/reference/delete-mortgagee", variables, {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        });
      }
      if (hasSelected) {
        delete variables.mode;
        return await myAxios.post("/reference/update-mortgagee", variables, {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        });
      }
      delete variables.mode;
      return await myAxios.post("/reference/add-mortgagee", variables, {
        headers: {
          Authorization: `Bearer ${user?.accessToken}`,
        },
      });
    },
    onSuccess: (res) => {
      if (res.data.success) {
        queryClient.invalidateQueries(queryKey);
        return Swal.fire({
          position: "center",
          icon: "success",
          title: res.data.message,
          showConfirmButton: false,
          timer: 1500,
        });
      }

      Swal.fire({
        position: "center",
        icon: "error",
        title: res.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
    },
  });

  useEffect(() => {
    if (searchParams.get("selected") === "") {
      return;
    }
    const selectedParams = searchParams.get("selected") as string;
    const selectedIDs = new Set([selectedParams]);
    const selectedRowData = rows?.filter((row: any) =>
      selectedIDs.has(row.Mortgagee.toString())
    )[0];
    if (selectedRowData) {
      setNewStateValue(dispatch, selectedRowData);
    }
  }, [rows, searchParams]);

  function handleSearchChange(event: any) {
    setLoadingSearch(true);
    const newInputValue = event.target.value;
    setSearchParams((prev) => {
      prev.set("mortgageeSearch", newInputValue);
      return prev;
    });

    myAxios
      .get(`/reference/search-mortgagee?mortgageeSearch=${newInputValue}`, {
        headers: { Authorization: `Bearer ${user?.accessToken}` },
      })
      .then((res) => {
        console.log(res);
        if (res.data.success) {
          setLoadingSearch(false);
          setRows(res.data.mortgagee);
        }
      });
  }

  const handleInputSelectionChange = (data: any) => {
    const { name, value } = data;
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  const handleInputChange = (e: any) => {
    const { name, value } = e.target;
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  function handleOnSave() {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, save it!",
    }).then((result) => {
      if (result.isConfirmed) {
        wait(300).then(() => {
          submitButton.current?.click();
        });
      }
    });
  }

  async function handleSubmit(e: FormEvent) {
    e.preventDefault();
    mutate({ ...state, mode: "" });
  }

  console.log(data);
  const handleInputChangeThrottled = throttle(handleSearchChange, 300);
  return (
    <React.Fragment>
      <Box>
        <Typography variant="h5" sx={{ marginBottom: "10px" }}>
          Mortgagee Details
        </Typography>
      </Box>
      <Box
        sx={(theme) => ({
          display: "flex",
          alignItems: "center",
          columnGap: "20px",
          [theme.breakpoints.down("sm")]: {
            flexDirection: "column",
            alignItems: "flex-start",
            flex: 1,
            marginBottom: "15px",
          },
        })}
      >
        <div
          style={{
            marginTop: "10px",
            marginBottom: "12px",
            flex: 1,
            width: "100%",
          }}
        >
          <TextField
            label="Search"
            fullWidth
            size="small"
            type="text"
            value={searchParams.get("mortgageeSearch")}
            onChange={handleInputChangeThrottled}
          />
        </div>
        {exportMode ? (
          <div
            style={{
              display: "flex",
              alignItems: "center",
              columnGap: "5px",
            }}
          >
            <Button
              variant="outlined"
              color="success"
              size="small"
              onClick={() => {
                import("../../../lib/export").then((m) => {
                  m.exportFunction(
                    `/reference/export-mortgagee?mortgageeSearch=${searchParams.get(
                      "mortgageeSearch"
                    )}&isAll=true`,
                    `Mortgagee-All.xlsx`,
                    myAxios,
                    user
                  );
                });
              }}
            >
              Export All
            </Button>
            <Button
              variant="outlined"
              color="success"
              onClick={() => {
                import("../../../lib/export").then((m) => {
                  m.exportFunction(
                    `/reference/export-mortgagee?mortgageeSearch=${searchParams.get(
                      "mortgageeSearch"
                    )}&isAll=false`,
                    `Mortgagee-Search.xlsx`,
                    myAxios,
                    user
                  );
                });
              }}
              size="small"
            >
              Export Table
            </Button>
            <Button
              variant="outlined"
              color="error"
              size="small"
              onClick={() => setExportMode(false)}
            >
              Cancel
            </Button>
          </div>
        ) : (
          <div
            style={{
              display: "flex",
              alignItems: "center",
              columnGap: "20px",
            }}
          >
            <div
              style={{
                display: "flex",
                alignItems: "center",
                columnGap: "5px",
              }}
            >
              <div>
                <Dropdown setExportMode={setExportMode} />
              </div>
              {!save && (
                <Button
                  variant="contained"
                  startIcon={<AddIcon />}
                  id="entry-header-save-button"
                  onClick={() => {
                    setAddNew(true);
                    setSave(true);
                    setHasSelected(false);
                    setSearchParams((prev) => {
                      prev.set("policySearch", "");
                      prev.set("selected", "");
                      return prev;
                    });
                    document
                      .getElementById("reset-data-grid-selection")
                      ?.click();
                  }}
                >
                  New
                </Button>
              )}

              <Box sx={{ position: "relative" }}>
                <Button
                  id="save-entry-header"
                  color="primary"
                  variant="contained"
                  type="submit"
                  onClick={handleOnSave}
                  disabled={
                    (loadingAddNew && variables.mode === undefined) || !save
                  }
                  startIcon={<SaveIcon />}
                >
                  Save
                </Button>

                {loadingAddNew && variables.mode === undefined && (
                  <CircularProgress
                    size={24}
                    sx={{
                      color: green[500],
                      position: "absolute",
                      top: "50%",
                      left: "50%",
                      marginTop: "-12px",
                      marginLeft: "-12px",
                    }}
                  />
                )}
              </Box>
              {save && (
                <Button
                  variant="contained"
                  startIcon={<CloseIcon />}
                  color="error"
                  onClick={() => {
                    Swal.fire({
                      title: "Are you sure?",
                      text: "You won't be able to revert this!",
                      icon: "warning",
                      showCancelButton: true,
                      confirmButtonColor: "#3085d6",
                      cancelButtonColor: "#d33",
                      confirmButtonText: "Yes, cancel it!",
                    }).then((result) => {
                      if (result.isConfirmed) {
                        setSave(false);
                        setAddNew(false);
                        setDeleting(false);
                        setNewStateValue(dispatch, initialState);
                        document
                          .getElementById("reset-data-grid-selection")
                          ?.click();
                        setHasSelected(false);
                        setSearchParams((prev) => {
                          prev.set("policySearch", "");
                          prev.set("selected", "");
                          return prev;
                        });
                      }
                    });
                  }}
                >
                  Cancel
                </Button>
              )}

              <Button
                id="save-entry-header"
                variant="contained"
                sx={{
                  backgroundColor: pink[500],
                  "&:hover": {
                    backgroundColor: pink[600],
                  },
                }}
                disabled={!deleting}
                startIcon={<DeleteIcon />}
                onClick={() => {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, delete it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      mutate({
                        Mortgagee: searchParams.get("selected") as string,
                        mode: "delete",
                      });
                    } else {
                    }
                  });
                }}
              >
                Delete
              </Button>
            </div>
          </div>
        )}
      </Box>
      {!exportMode && (
        <form onSubmit={handleSubmit}>
          <Box
            sx={(theme) => ({
              display: "flex",
              columnGap: "15px",
              flexDirection: "row",
              [theme.breakpoints.down("md")]: {
                flexDirection: "column",
                rowGap: "10px",
              },
            })}
          >
            {!isLoading && (
              <React.Fragment>
                <Autocomplete
                  disabled={!addNew}
                  value={state.Policy}
                  onChange={(event: any, value: string | null) => {
                    handleInputSelectionChange({ name: "Policy", value });
                  }}
                  size="small"
                  freeSolo
                  disableClearable
                  options={(data as any).data.mortgagee.policy.map(
                    (option: any) => option.Policy
                  )}
                  getOptionLabel={(option: any) => option}
                  sx={(theme) => ({
                    width: 300,
                    [theme.breakpoints.down("md")]: { width: "100%" },
                  })}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Policy"
                      name="Policy"
                      InputProps={{
                        ...params.InputProps,
                        type: "search",
                      }}
                      onChange={handleInputChange}
                    />
                  )}
                />
                <Autocomplete
                  disabled={!addNew || hasSelected}
                  value={state.Mortgagee}
                  onChange={(event: any, value: string | null) => {
                    handleInputSelectionChange({ name: "Mortgagee", value });
                  }}
                  size="small"
                  freeSolo
                  disableClearable
                  options={(data as any).data.mortgagee.mortgagee.map(
                    (option: any) => option.Mortgagee
                  )}
                  getOptionLabel={(option: any) => option}
                  sx={(theme) => ({
                    width: 700,
                    [theme.breakpoints.down("md")]: { width: "100%" },
                  })}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Mortgagee"
                      name="Mortgagee"
                      InputProps={{
                        ...params.InputProps,
                        type: "search",
                      }}
                      onChange={handleInputChange}
                    />
                  )}
                />
              </React.Fragment>
            )}
          </Box>
          <button
            ref={submitButton}
            style={{ display: "none" }}
            type="submit"
          ></button>
        </form>
      )}

      <div style={{ marginTop: "10px", width: "100%", position: "relative" }}>
        <DataGridTable
          mutipleSelect={exportMode}
          isLoading={loadingAddNew || isLoading || loadingSearch}
          dataKey={"entry"}
          queryKey={[queryKey]}
          columns={poliyAccountColumn}
          onSelectionChange={(rowSelectionModel, data) => {
            if (rowSelectionModel.length <= 0) {
              setNewStateValue(dispatch, initialState);
              setSearchParams((prev) => {
                prev.set("selected", "");
                return prev;
              });
              setHasSelected(false);
              setAddNew(false);
              setSave(false);
              setDeleting(false);
              return;
            }
            setSave(true);
            setDeleting(true);
            setAddNew(true);
            setHasSelected(true);

            const selectedIDs = new Set(rowSelectionModel);
            const selectedRowData = data.filter((row: any) =>
              selectedIDs.has(row.Mortgagee.toString())
            );

            setSearchParams((prev) => {
              prev.set("selected", rowSelectionModel[0] as string);
              return prev;
            });

            setNewStateValue(dispatch, selectedRowData[0]);
          }}
          getRowId={(row) => row.Mortgagee}
          rows={rows}
          setRows={setRows}
        />
      </div>
    </React.Fragment>
  );
}
export function setNewStateValue(dispatch: any, obj: any) {
  Object.entries(obj).forEach(([field, value]) => {
    dispatch({ type: "UPDATE_FIELD", field, value });
  });
}
