import React from "react";
import { Button, Menu, MenuItem } from "@mui/material";
import { green } from "@mui/material/colors";

export function Dropdown({
  setExportMode,
}: {
  setExportMode: React.Dispatch<React.SetStateAction<boolean>>;
}) {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
    setExportMode(true);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <React.Fragment>
      <Button
        id="basic-button"
        aria-controls={open ? "basic-menu" : undefined}
        aria-haspopup="true"
        aria-expanded={open ? "true" : undefined}
        onClick={handleClick}
        variant="contained"
        sx={{
          backgroundColor: green[500],
          "&:hover": {
            backgroundColor: green[600],
          },
        }}
      >
        Export
      </Button>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
      >
        <MenuItem onClick={handleClose}>Export as Excel All Data </MenuItem>
        <MenuItem onClick={handleClose}>
          Export as Excel Only Data in Table{" "}
        </MenuItem>
        <MenuItem onClick={handleClose}>Print Data in Table</MenuItem>
      </Menu>
    </React.Fragment>
  );
}
